<template>
  <div class="goods">
<van-nav-bar
  title=""
  left-text=""
:fixed='bol'
  :left-arrow='bol'
  @click-left="tui"
 
/>
<van-pull-refresh v-model="isLoading" @refresh="onRefresh" pulling-text='' loosing-text='~'>
    <van-swipe class="goods-swipe" :autoplay="3000">
      <van-swipe-item v-for="v in arr" :key="v">
        <img :src="arr.poster" >
      </van-swipe-item>
    </van-swipe>

    <van-cell-group>
      <van-cell>
        <div class="goods-title">{{ arr.title }}</div>
        <div class="goods-price">{{ arr.price }}</div>
      </van-cell>
      <van-cell class="goods-express">
        <van-col span="10"></van-col>
        <van-col span="14">999+</van-col>
      </van-cell>
    </van-cell-group>

    <van-cell-group class="goods-cell-group">
      <van-cell value="" icon="shop-o" is-link @click="sorry">
        <template slot="title">
          <span class="van-cell-text"></span>
          <van-tag class="goods-tag" type="danger"></van-tag>
        </template>
      </van-cell>
      <van-cell title="" icon="location-o" is-link @click="sorry" />
    </van-cell-group>
<van-collapse v-model="activeName" accordion>

  <van-collapse-item title="" name="2">
          {{arr.releasetime}}<br>
      {{arr.city}}{{arr.address}}<br>
      {{arr.profermer_detial}}<br>
      {{arr.content}}
  </van-collapse-item>

</van-collapse>

    <van-goods-action style="bottom: 65px;">
      <van-goods-action-icon icon="chat-o" @click="sorry">
        
      </van-goods-action-icon>
      <van-goods-action-icon icon="cart-o" @click="onClickCart">
        
      </van-goods-action-icon>
      <van-goods-action-button type="warning" @click="sorry">
        
      </van-goods-action-button>
      <van-goods-action-button type="danger" @click="sorry">
        
      </van-goods-action-button>
    </van-goods-action>
     </van-pull-refresh>
    <div style="height:70px;width:100%"></div>
  </div>
</template>

<script>
import Myheader from '../../components/header'
import {getdamai} from '../../api'
import {
  Tag,
  Col,
  Icon,
  Cell,
  CellGroup,
  Swipe,
  Toast,
  SwipeItem,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton
} from 'vant';
export default {
  components: {
    [Tag.name]: Tag,
    [Col.name]: Col,
    [Icon.name]: Icon,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [GoodsAction.name]: GoodsAction,
    [GoodsActionIcon.name]: GoodsActionIcon,
    [GoodsActionButton.name]: GoodsActionButton,
    Myheader
  },
  data() {
    return {
        arr:[],
        index:this.$route.params.id,
        count: 0,
        isLoading: false,
        activeName: '',
         bol:true
    };
  },
      mounted(){
        
        this.fn(this.$route.params.id);
    },
  methods: {
    fn(index){
    getdamai(`http://47.99.153.121:8021/index.php/index/detailsContent/did/${index}`)
            .then(res=>{
                this.arr=res[0];
            })
        },
    onClickCart() {
       Toast('~');
    },
    sorry() {
      Toast('~');
    },        
    tui(){
            this.$router.go(-1);
        },
    onRefresh() {
      setTimeout(() => {
        this.$toast('~');
        this.isLoading = false;
        this.count++;
      }, 500);
    }
  }
};
</script>

<style lang="less">
.goods {
  padding-bottom: 50px;
  &-swipe {
    img {
      width: 100%;
      display: block;
    }
  }
  &-title {
    font-size: 16px;
  }
  &-price {
    color: #f44;
  }
  &-express {
    color: #999;
    font-size: 12px;
    padding: 5px 15px;
  }
  &-cell-group {
    margin: 15px 0;
    .van-cell__value {
      color: #999;
    }
  }
  &-tag {
    margin-left: 5px;
  }
}
.van-icon-arrow-left {
    
    
    left: 0px;
}
</style>


