
<template>
<div>
<Myheader bol='true'>
<img src='../../assets/images/ic_chat_white.png' class='rightImg' slot="right">
<div slot="search" class="search">
    <input type="text" placeholder="   ">
    <img src='../../assets/images/ic_group_search_small.png' class="searchImg">
    <img src='../../assets/images/ic_scan_gray.png' class="scanImg">
</div>
</Myheader>
<van-pull-refresh v-model="isLoading" @refresh="onRefresh" pulling-text='' loosing-text='~'>
<Banner swiperid='go' paginationdesition='right' >
    <div slot="swiper-con" class="swiper-slide" >
        <img src="../../assets/images/banner/01.jpg" alt="">
    </div>
        <div slot="swiper-con" class="swiper-slide">
        <img src="../../assets/images/banner/02.jpg" alt="">
    </div>
        <div slot="swiper-con" class="swiper-slide">
        <img src="../../assets/images/banner/03.jpg" alt="">
    </div>
</Banner>  
<Cell title='' :hot='true'></Cell>
<van-list
  v-model="loading"
  :finished="finished"
  finished-text=""
  @load="onLoad"
>
<List v-for="(item,index) in arr" :url='"http:"+item.image_url' :key='index'>
    <span slot='title' @click='tishi(item.item_id)'>{{item.title|title}}</span>
    <span slot='desc' @click='tishi(item.item_id)'>:{{item.source}}</span>
</List>
</van-list>
</van-pull-refresh>
 <div style="height:80px;width:100%"></div> 
</div>
</template>
<script>

import Cell from '../../components/cell'
import Myheader from '../../components/header'
import Banner from '../../components/banner'
import List from '../../components/newslist'
export default {
    data(){
        return{
        arr:[],
        count: 0,
        isLoading: false,
        loading: false,
        finished: false
        }
    },
    components:{
        Myheader,
        Banner,
        Cell,
        List
    },
    created() {
         this.fn();
    },
    methods: {
        fn(){
        this.toutiao()
        .then(res=>this.arr=res)//api.js
        },
        tishi(i){
        this.$router.push('/detail/'+i)
        },
        onRefresh() {
        setTimeout(() => {
        this.fn();
        this.$toast.success('');
        this.isLoading = false;
        this.count++;
      }, 500);
    },
        onLoad() {
      // 
      setTimeout(() => {
        for (let i = 0; i < 6; i++) {
        this.toutiao()
        .then(res=>{
            res.forEach((v,i) => {
                this.arr.push(res[i]);
            });
        })
        ;
        }
        // 
        this.loading = false;
        // 
        if (this.arr.length >= 60) {//60
          this.finished = true;
        }
      }, 1000);
      }
    
    },
}
</script>






